import koaRouter from 'koa-router';

/** @type {Object}  (koa-router) */
export const router = koaRouter();

/** @type {Object}  */
export default router.routes();

// ----------------------------------------------------------------------------

router.get('/api/timestamp', async ctx => {
    ctx.set('Access-Control-Allow-Origin', '*');
    ctx.body = {
        ts: Date.now()
    };
});
